  height: auto !important;
  overflow: hidden !important;
  max-width: none !important;
  float: none !important;
  padding: 0 !important;
  /* 		margin: 				5px 1% !important; */
  margin: 5px 0 !important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: stretch;
  align-items: stretch;
  -ms-flex-line-pack: stretch;
  align-content: stretch;
}

ul._pi_answers_container li:hover {
  /* 		background-color: initial; */
}

/* ------------------------------ SINGLE CHOICE ------------------------------ */

ul._pi_answers_container li a {
  /* 			color: initial; */
  /* 			font-family: initial; */
  /* 			font-size: initial; */
  /* 			font-weight: initial; */
  /* 			text-decoration: initial; */
  /* 			margin: initial; */
  /* 			height: initial; */
  padding: 0 !important;
  height: auto;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -ms-flex-preferred-size: 100%;
  flex-basis: 100%;
  position: static;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: center;
  align-content: center;
  min-width: 100% !important;
}

ul._pi_answers_container li a span._pi_radio_button_outer {
  /* 				top: initial; */
  /* 				left: initial; */
  /* 				float: initial; */
  /* 				display: block; */
  /* 				flex: 0 0 14px; */
  /* 				flex-grow: 0; */
  /* 				flex-shrink: 0; */
  /* 				flex-basis: 14px; */
  position: static;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 10px;
  /* make sure it matches the variable for checkbox right and left margin */
  margin-right: 0;
  /* VISUAL PARAMETERS - CAN BE OVERRIDDEN BY CIENT USING !IMPORTANT */
  width: 14px;
  min-width: 14px;
  max-width: 14px;
  height: 14px;
  min-height: 14px;
  max-height: 14px;
  background-color: rgba(255, 255, 255, 0.2);
  /* backup value */
  border: 1px solid rgba(255, 255, 255, 0.5);
  /* backup value */
  border-radius: 50%;
}

ul._pi_answers_container li a span._pi_radio_button_inner,
ul._pi_answers_container li:hover a span._pi_radio_button_inner {
  position: relative;
  display: block;
  width: 8px;
  min-width: 8px;
  max-width: 8px;
  height: 8px;
  min-height: 8px;
  max-height: 8px;
  top: 2px;
  left: 2px;
  border-radius: 50%;
}

ul._pi_answers_container li:hover a span._pi_radio_button_inner {
  background-color: rgba(255, 255, 255, 0.5);
  /* backup value */
  border: 1px solid transparent;
  /* backup value */
}

ul._pi_answers_container li a label {
  padding: 8px 15px;
  /* Match it to multi-choice label right padding ONLY */
  margin: 0 !important;
  min-width: 100%;
  width: auto !important;
}

/* ------------------------------ MULTIPLE CHOICE ------------------------------ */

/* Text */

ul._pi_answers_container li label._pi-control-checkbox {
  /* 			width: initial; */
  /* 			float: initial; */
  /* 			margin: initial; */
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -ms-flex-preferred-size: 100%;
  flex-basis: 100%;
  position: static;
  overflow: hidden;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
  -ms-flex-line-pack: start;
  align-content: flex-start;
  padding-top: 8px;
  /* Match to single-choice */
  padding-bottom: 8px;
  /* Match to single-choice */
  padding-left: 0;
  /* Do NOT make it a variable. There's a margin already in checkbox  */
  padding-right: 15px;
  /* Match it to single-choice label right & left padding */
}

/* Hidden Checkbox Form Element (nothing) */

ul._pi_answers_container li label input._pi_checkbox {}

/* Simulated Checkbox */

._pi-control-indicator {
  position: static;
  display: block;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -ms-flex-preferred-size: 14px;
  flex-basis: 14px;
  margin-top: 2px;
  /* CREATE a variable for vertical adjust of the box */
  margin-bottom: 0;
  margin-left: 10px;
  /* Make sure it matches the variable for radio left margin */
  margin-right: 10px;
  /* Make sure it matches the variable for radio left margin */
  width: 14px;
  height: 14px;
  border: 1px solid #000;
}

/* Simulated Checkmark (nothing) */

._pi-control input:checked~._pi-control-indicator:after {}

._pi-control-checkbox ._pi-control-indicator:after {
  left: 4px;
  width: 4px;
  height: 10px;
  border: solid #000;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

._pi-control-indicator:after {
  content: '';
  position: relative;
  display: none;
}

